import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import img1 from "../img/about/about-1.jpg";
import img2 from "../img/about/Capture dcran 202442111.png";
const Aboutshort = () => {
  const options = {
    loop: true,
    autoplay: true,
    smartSpeed: 1000,
    dots: false,
    nav: true,
    navText: [
      "<span><i class='fa fa-long-arrow-left '></i> prev</span>",
      "<span>next <i class='fa fa-long-arrow-right '></i></span>",
    ],
    responsive: {
      0: {
        items: 1,
      },
      600: {
        items: 1,
      },
      1000: {
        items: 1,
      },
    },
  };
  return (
    <>
      <div className="about-area sec-p-100">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <h2> propos de notre histoire</h2>
              <p>
              Elle est cre en 2016, la socit HASSAIRI STEEL INDUSTRIE a russi  se faire
              tailler une part de march importante dans le domaine de construction mtallique. Ceci
              revient en particulier au savoir faire de son quipe dirigeants, au savoir faire acquis depuis
              et mme avant sa cration ainsi que de la qualit des produits et services fournis. Mr
               Housseleddine HASSAIRI ,  la tte de la socit HSI est de formation universitaire
              est spcialise en construction mtallique. Avant dentamer son propre projet, il a cumul
              une carrire professionnelle de plus de 10 ans (depuis 2005) au sein de socits familiales
              spcialises en la construction mtallique et le commerce des produits mtallurgique{" "}
              </p>
              <a href="#" className="btn btn-type-5 mt-30">
                about more
              </a>
            </div>
            <div className="col-md-6">
              <div className="about-slider-wraper">
                <OwlCarousel className="owl-theme owl-carousel" {...options}>
                  <div className="about-single-item">
                    <img src={img1} alt="about-img" className="img-fluid" />
                    <h4>industrial experimental technologies</h4>
                  </div>
                  <div className="about-single-item">
                    <img src={img2} alt="about-img" className="img-fluid" />
                    <h4>industrial experimental technologies</h4>
                  </div>
                </OwlCarousel>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Aboutshort;
